/* @import "~antd/dist/antd.css"; */

:root {
  --background-color: #000000;
  --border-radius: 0px;
  --primary-color: #592e84;
  --secondary-color: #222222;
  --input-background-color: #2b2b2b;
  --padding: 1.1rem;
  --border-color: #9797975e;
  --max-width: 1600px;
  --width-percentage: 90%;
  --global-font-family: "Nunito Sans", "Inter", sans-serif;
}

body {
  font-family: var(--global-font-family);
  margin: 0;
  font-size: 16px;
  color: white;
  background-color: var(--background-color);
}

* {
  box-sizing: border-box;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

a {
  text-decoration: none;
  color: white;
}

a:visited {
  color: white;
}

a:hover {
  text-decoration: underline;
}

/* Navigation Bar Start */
nav {
  background-color: var(--secondary-color);
  height: 66px;
  position: fixed;
  left: 0px;
  width: 100%;
  color: white;
  top: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  border-bottom: 1px solid var(--input-background-color);
  /* border-bottom: 1px solid #BDB8B8; */
}

.nav_content {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: flex-start;
  width: var(--width-percentage);
  max-width: var(--max-width);
}

.site_title {
  font-size: 17px;
  margin: 0px;
  margin-left: 22px;
  font-weight: bold;
}

.nav_button {
  outline: none;
  margin-left: auto;
  height: 100%;
  width: 50px;
  border: none;
  background-color: transparent;
  transition: fill 0.2s;
  fill: var(--border-color);
}

.nav_button:hover {
  fill: white;
  cursor: pointer;
}

.nav-content > ul:first-child {
  margin: 0px;
  margin-left: auto;
  list-style-type: none;
  padding: 0px;
}

