}

.nav-content li a {
  display: block;
  text-decoration: none;
  font-size: 1rem;
  font-weight: 700;
}

.hm {
  display: none
} */

/* Navigation Bar End */

/* Footer start */
footer {
  position: fixed;
  border-top: 1px solid var(--input-background-color);
  bottom: 0px;
  left: 0px;
  right: 0px;
  display: flex;
  z-index: 999;
  background-color: var(--secondary-color);
  height: 60px;
  justify-content: center;
  align-items: center;
  color: white;
}

.footer_content {
  padding-left: 0px;
  height: 100%;
  width: var(--width-percentage);
  max-width: var(--max-width);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin: 0px;
  list-style-type: none;
  padding: 0px;
}

.footer_content .anticon {
  display: block;
}

.footer_content span,
.footer_content h4 {
  display: none;
}

.footer_nav {
  padding-left: 0px;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin: 0px;
  list-style-type: none;
  padding: 0px;
}

.footer_nav li {
  /* display: inline-block; */
  margin: 0px;
  height: 100%;
}

.footer_nav li a {
  display: flex;
  width: 50px;
  flex-direction: column;
  align-items: center;
  align-content: space-between;
  height: 100%;
  justify-content: space-evenly;
}

.footer_nav p {
  margin: 0px;
  font-size: 10px;
  text-align: center;
  font-weight: 400;
}

/* make footer stoff appear */
.footer_content iframe,
.footer_content h4 {
  display: none;
}

