.footer-content li a:visited {
  color: inherit;
} */

.footer-content li a:hover {
  text-decoration: underline;
}

/* footer end */

main {
  flex: 1;
  /* padding-bottom: 40px; */
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  margin-top: 66px;
  margin-bottom: 20px;
  background-color: var(--background-color);
}

.main_content {
  color: white;
  max-width: var(--max-width);
  width: var(--width-percentage);

  /*   
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start; */
}

/* controls start */
.controls,
.controls *::before,
.controls *::after {
  box-sizing: border-box;
}

.controls {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  min-height: 70px;
  box-sizing: border-box;
  width: 100%;
}
.controls_detail {
  margin-top: 15px;
  margin-right:20px;
  display: flex;
  flex-direction: column;
  /* width:30%; */
  min-width: 150px;

}

.controls h3 {
  margin: 0px;
  margin-top: 5px;
  font-size: 19px;
  font-weight: 600;
}

.controls span {
  font-size: 10px;
}

.controls form {
  width: 100%;
  margin: 0px;
  margin-top: 10px;
}

.controls form select {
  height: 44px;
  font-size: 15px;
  padding-left: 8px;
  font-weight: 600;
  border: 2px solid var(--border-color);
  color: white;
  outline: none;
  width: 100%;
  /* https://stackoverflow.com/a/27641804 */
  background: url("./imgs/angle-down.svg") no-repeat right;
  -webkit-appearance: none;
  appearance: none;
  -moz-appearance: none;
  background-position-x: calc(100% - 10px);
  background-color: var(--input-background-color);
}

.controls form select:focus {
  border: 2px solid white;
}

/* controls end  */

/* menu start */

.menu {
  padding: 10px;

  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid var(--border-color);
  color: white;
  background-color: var(--secondary-color);
  background-image: url("./imgs/Mholan_Logo.png");
  background-size: 70px 50px;
  background-repeat: no-repeat;
  background-position: right bottom;
  /* background-size: 24%; */
  position: absolute;
  /* display: none; */
  top: 66px;
  left: 0px;
  right: 0px;
  transition-property: visibility, opacity;
  transition-duration: 2s, 2s;
}

.menu_content {
  width: var(--width-percentage);
  max-width: var(--max-width);
}

.nav_button svg,
.menu_content svg{
  width: 1.5em;
  height: 1.5em;
  fill:#999;
}

.nav_button svg:hover,
.menu_content svg:hover {
  fill:#fff;
}

.closed_menu {
  display: none;
}

.open_menu {
  display: flex;
}

.menu ul {
  justify-content: flex-start;
  padding-left: 0px;
  flex-wrap: wrap;
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
}

.menu li {
  display: inline;
  margin-right: 20px;
}

.menu li a {
  font-size: 18px;
  font-weight: 500;
}

.menu p {
  font-size: 15px;
}

/* menu end */

