#count_total_cases {
  grid-area: tc;
  position: relative;
}

.grid_box_inner_content {
  text-align: left;
  padding: 10px;
}

.grid_box_inner_content p {
  font-size: 14px;
  font-weight: 100;
}

.grid_box_inner_content h3 {
  font-size: 40px;
  margin: 0px;
}

.graph_heading {
  
  padding: 14px;

  font-size: 14px;
  font-weight: bold;
}

.graph_container {
  position: relative;
  flex: 1
}


#count_total_deaths {
  position: relative;
  grid-area: td;
}
#count_total_recoveries {
  position: relative;
  grid-area: tr;
}

#cases_today {
  grid-area: ct;
}

#cases_recovered {
  grid-area: cr;
}

#graph_fatality {
  grid-area: gf;
  position: relative;
}

#graph_recovery {
  grid-area: gr;
  position: relative;
}

/* local graphs */
#local_graph_fatality {
  display: flex;
  flex-direction: column;
  grid-area: lgf;
  position: relative;
}

#local_graph_recovery {
  display: flex;
  flex-direction: column;
  grid-area: lgr;
  position: relative;
}

#local_graph_cases {
  display: flex;
  flex-direction: column;
  grid-area: lgc;
  position: relative;
}

/* global graphs */
#global_graph_fatality {
  display: flex;
  flex-direction: column;
  grid-area: ggf;
  position: relative;
}

#global_graph_recovery {
  display: flex;
  flex-direction: column;
  grid-area: ggr;
  position: relative;
}

#global_graph_cases {
  display: flex;
  flex-direction: column;
  grid-area: ggc;
  position: relative;
}

#map {
  grid-area: map;
  position: relative;
}

.map_content {
  position: absolute;
  top: 2px;
  bottom: 2px;
  left: 2px;
  right: 2px;
}

/* popup tooltip of map */
.popup {
  color: black;
}

.popup p {
  margin: 5px;
}

.mapboxgl-popup-content {
  padding: 10px;
}

#news {
  grid-area: news;
}

#twitterfeed {
  grid-area: twit;
  position: relative;
}

#table {
  grid-area: table;
  position: relative;
}



.table_content {
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;
  /* text-align: start;
 display: grid;
 grid-template-columns: 2fr repeat(3, 1fr); */
}

.table_content tbody {
}

/* https://css-tricks.com/position-sticky-and-table-headers/ */
.table_content th {
  background-color: rgb(210, 54, 54);
  position: sticky;
  top: 0px;
  text-align: start;
}

.table_content thead {
  background-color: rgb(210, 54, 54);
}

.table_content tr:hover {
  background-color: #363636;
  cursor: pointer;
}

.table_content td,
.table_content th {
  padding: 10px;
  white-space: nowrap;
}

