@media only screen and (min-width: 848px) {

  .controls {
    display: inline-flex;
    flex-direction: row;
  }

  .graph_heading { 

    padding: 14px;
    font-size: 18px;
  }

  .about {
    font-size: 20px;
  }

  .controls form select {
    width: 100%;
    font-size: 20px;
  }

  .controls h3 {
    display: inline;
    font-size: 17px;
  }

  .controls span {
    font-size: 11px;
  }

  /* footer will not be fixed */

  footer {
    position: static;

    /* bottom: auto;
    left: auto;
    right: auto; */
  }

  /* hider burger */
  .nav_button {
    margin-top: 3px;
    display: block;
  }

  /* make menu normal */
  .menu_content,
  .menu {
    /* all: unset; */
    width: auto;
    height: 100%;
  }

  .open_menu {
    all: unset;
  }

  /* set it to be on right side always */
  .menu {
    /* margin-left: auto; */
    margin-left: 10px;
    background-image: none;
  }

  /* ghaib karo github wale span aur paragraph ko ko */
  .menu p,
  .menu span {
    display: none;
  }

  .menu .anticon {
    display: block;
  }

  /* ul ke margin ko hatao */
  .menu_content ul {
    margin: 0px;
    display: flex;
    height: inherit;
    align-items: center;
  }

  /* hide github stuff */

  .menu_content iframe {
    display: none;
  }

  /* make the anchor full height to make it easy to click */
  .menu_content ul li {
    height: inherit;
  }

  .menu_content ul li a {
    height: inherit;
    display: flex;
    align-items: center;
  }

  /* enlarge site title */
  .site_title {
    font-size: 24px;
  }

  /* increase height of footer */
  footer {
    height: 60px;
  }

  /* make footer stoff appear */
  .footer_content iframe,
  .footer_content h4 {
    display: initial;
  }

  /* make ul right aligned */
  .footer_nav {
    margin-left: auto;
    width: unset;
